.SwitchButton {
			padding: 5px;
			border: none;
			border-radius: 4px;
			background-color: #fff;
			transition: 0.5s;
		}
.SwitchButton:hover {
	box-shadow: 4px 2px rgba(0,0,0,0.2);
	transition: 0.5s;
}

.toggledSwitchButton {
	box-shadow: 4px 2px rgba(0,0,0,0.2);
	background-color: #fbf1be;
}

